import { Component, OnInit } from "@angular/core";
import { Beyond } from "../../../../../../utilities/beyond";
import { ActivatedRoute, Router } from "@angular/router";
import { Http, Headers, RequestOptions } from "@angular/http";
import { Global } from "../../../../../../global/global";
import { ToastrService } from "ngx-toastr";
@Component({
  providers: [Beyond],
  selector: 'app-view-write-ups',
  templateUrl: './view-write-ups.component.html',
  styleUrls: ['./view-write-ups.component.scss']
})
export class ViewWriteUpsComponent implements OnInit {

  viewWriteupData: any;

  viewWriteupStatus: boolean = false;

  id: any;

  logo1: string = "assets/img/no-image.png";


  // viewLiveEventEndDate: any;
  // finalEndTime: string;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.id = this.activatedRoute.snapshot.params["id"];
    this.viewWriteupDetails();
  }


  okWriteups() {
    this.router.navigate(['/proStaff/viewStaff2'])
  }

  viewWriteupDetails() {
    //console.log("viewPage Called" + this.id);
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/viewWriteup/" + this.id, options).subscribe(
      data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewWriteupStatus = true;
          this.viewWriteupData = response.response[0];
          // this.hours = this.openTime.split(':')[0]
          // this.minutes = this.openTime.split(':')[1]
          // this.finalOpenTime = this.hours + ":" + this.minutes
          // console.log("hours" + JSON.stringify(this.finalOpenTime))
          // this.viewLiveEventEndDate = this.viewLiveEventsData.endTime;
          // this.hours = this.viewLiveEventEndDate.split(':')[0]
          // this.minutes = this.viewLiveEventEndDate.split(':')[1]
          // this.finacloseTime = this.hours + ":" + this.minutes
          // console.log("hours" + JSON.stringify(this.finalEndTime))
          //console.log("ram", this.viewSponsorData1);
          this.toastr.success("View Writeup Details");
          this.viewWriteupData.pdfName = "";
          if (this.viewWriteupData.writeupsDocument) {
            this.viewWriteupData.pdfName = this.viewWriteupData.writeupsDocument;
            this.viewWriteupData.writeupsDocument =
              Global.url +
              "/pdf/" +
              this.viewWriteupData.writeupsDocument;
          }

          console.log("ram1", this.viewWriteupData);
        }
      },
      error => {
        console.log("The execution is failed " + JSON.stringify(error));
      }
    );
  }
}
