import { Http} from "@angular/http";
import { ToastrService } from "ngx-toastr";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import {FormBuilder,FormGroup,Validators} from "@angular/forms";

import { ImageUpload } from "src/utilities/imageupload";
import { Global } from "../../../../../../global/global";
import { Beyond } from "../../../../../../utilities/beyond";

@Component({
  providers: [Beyond, ImageUpload],
  selector: "app-add-write-ups",
  templateUrl: "./add-write-ups.component.html",
  styleUrls: ["./add-write-ups.component.scss"]
})
export class AddWriteUpsComponent implements OnInit {
  addWriteup: FormGroup;
  file: any;
  percentage: any;
  imagePath: string;
  submitted: boolean = false;
  progressShow: boolean = false;
  imagePreview: string = "assets/img/no-image.png";
  randamvalue: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    public toastr: ToastrService,
    public activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private http: Http
  ) {}

  ngOnInit() {
    this.beyond.beyondAdmin();
    const loginData = JSON.parse(localStorage.getItem("login"));
    this.addWriteup = this.formBuilder.group({
      writeups: ["", Validators.required],
      writeupsDocument: [""],
      employeeId: [""]
    });
  }

  get formErr() {
    return this.addWriteup.controls;
  }

  cancelWriteups() {
    this.router.navigate(["/proStaff/viewStaff2"]);
  }

  //Select Image - Starts Here
  select(event) {
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      this.file = file;
      this.showImagePreview(file);
    }
  }

  /*To show image preview*/
  showImagePreview(file) {
    const picReader = new FileReader();
    picReader.onload = (e: any) => {
      this.imagePreview = e.target.result;
    };
    picReader.readAsDataURL(file);
  }
  //HANDLEIMAGE(FILE) - ENDS HERE

  addwriteupData() {
    const viewDataValues = JSON.parse(localStorage.getItem("viewData"))
    const formData = new FormData();
    formData.append("writeups", this.addWriteup.value.writeups);
    formData.append("file", this.file);
    formData.append("employeeId",viewDataValues.employeeId);

    this.http.post(Global.url + "/addWriteups", formData).subscribe(
      data => {
        const response = data.json();
        if (response.status === 200) {
          this.toastr.success("Added WriteUp Data Successfully");
          this.router.navigate(["/proStaff/viewStaff2"]);
        } else {
          this.toastr.warning("INCORRECT DETAILS !");
        }
      },
      error => {
        this.toastr.warning("INCORRECT DETAILS !");
      }
    );
  }
  onSubmit() {
    this.submitted = true;
    if (this.addWriteup.value.writeups == "") {
      // this.toastr.warning("Please Enter Writeup comment.");
      return;
    } else {
      this.addwriteupData();
    }
  }
}
