import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListStaffComponent } from './proz-staff/list-staff/list-staff.component';
import { EditStaffComponent } from './proz-staff/edit-staff/edit-staff.component';
import { ViewStaff2Component } from './proz-staff/view-staff2/view-staff2.component';
import { AddWriteUpsComponent } from './proz-staff/view-staff2/writeups/add-write-ups/add-write-ups.component';
import { ViewWriteUpsComponent } from './proz-staff/view-staff2/writeups/view-write-ups/view-write-ups.component';
import { UploadDocumentComponent } from './proz-staff/view-staff2/documents/upload-document/upload-document.component';
import { ViewDocumentComponent } from './proz-staff/view-staff2/documents/view-document/view-document.component';

// STAFF COMPONENTS


import { StaffDashboardComponent } from './proz-staff/staff-dashboard/staff-dashboard.component';
import { StaffLayoutComponent } from './staff-layout/staff-layout.component';
import { AddStaff2Component } from './proz-staff/add-staff2/add-staff2.component';
const routes: Routes = [

  {
    path: '', component: StaffLayoutComponent,
    children: [

      {
        path: 'listStaff',
        component: ListStaffComponent
      },
      {
        path: 'viewStaff2',
        component: ViewStaff2Component
      },
      {
        path: 'editStaff',
        component: EditStaffComponent
      },
      {
        path: 'addWriteups',
        component: AddWriteUpsComponent
      },
      {
        path: 'viewWriteups',
        component: ViewWriteUpsComponent
      },
      {
        path: 'uploadDocument',
        component: UploadDocumentComponent
      },
      {
        path: 'viewDocument',
        component: ViewDocumentComponent
      },
      {
        path: 'addStaff2',
        component: AddStaff2Component
      },

      {
        path: 'staffDashboard',
        component: StaffDashboardComponent
      },

    ]
  }



];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StaffRoutingModule { }
