  listEmployeeDetails() {
    this.spinner.show();
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/employeeList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.spinner.hide();
          console.log("employee Details1" + JSON.stringify(response))
          this.employeeList = response.response;
          this.employeeList.forEach((e) => {
            /*TASK #542: List Staff Screen-- - When click on "TERMINATED/RESIGNED" -- - Staff Status Popup-- - The details will
   non - editable-- - Click on "REHIRE" button-- - When fill the fields and clicked "Save" button-- - Show popup to
   confirm rehire */
            if (e.managerName == null) {
              console.log("checking null")
              this.employeeList = response.response;
              //   e.managerName = e.managerName.replace(/\./g, ' ')
            }
          })
          /*TASK #542: List Staff Screen-- - When click on "TERMINATED/RESIGNED" -- - Staff Status Popup-- - The details will
   non - editable-- - Click on "REHIRE" button-- - When fill the fields and clicked "Save" button-- - Show popup to
   confirm rehire */

          console.log("list" + JSON.stringify(this.employeeList))
          this.searchStatus = this.employeeList[0].employeStatus
          this.showEmployeeData = true;
          return false;
        } else {
          this.spinner.hide();
          this.showEmployeeData = false;
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
