  /* upload documents file name */
  rehireStatus() {
    if (this.empDate == undefined) {
      this.Date = true;
    } else if (this.empDate != undefined) {
      this.Date = false;
    }
    // if (this.empRes == undefined) {
    //   this.Reason = true;
    // } else if (this.empRes != undefined) {
    //   this.Reason = false;
    // }
    // if (this.empfiles == undefined) {
    //   this.fileData = true;
    // } else if (this.empfiles != undefined) {
    //   this.fileData = false;
    // }
    if (this.empstatus == 'ACTIVE') {
      this.empData = true;
    } else if (this.empstatus != 'ACTIVE') {
      this.empData = false;
    }
    if (this.empDate != undefined && this.empstatus != 'ACTIVE') {
      console.log("ALL ARE FILLED")
      this.rehirepopup = 'none'

      this.displayFileModalRehireStatus = 'block'

      // this.displayFileModalmarkupStatus = 'block'

    }
    else {
      console.log("ALL ARE not FILLED")
      this.modelData = false;
    }
  }
  /* Get Pro Vanlynk Services */
  listProVanlynkServices() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/getProVanlynkServices/" + this.loginData.loginCode, options)
      .subscribe(
        data => {
          var response = data.json();
          if (response.status == 200) {
            this.enableVanlynkLicenses = response.response[0];
            console.log("EnableVanlynkLicenses:" + JSON.stringify(this.enableVanlynkLicenses));
            //console.log("ValueEnableVanlynkLicenses:" + JSON.stringify(this.enableVanlynkLicenses.value.noOfLicenses));
            if (this.enableVanlynkLicenses.noOfLicenses == 0) {
              alert("Add Vanlynk Licenses to Add Staff")
            }
            else if (this.enableVanlynkLicenses.noOfLicenses > 0) {
              this.StaffVanlynkServices()
            }

            return false;
          } else {
            this.toastr.warning("No Records Found");
          }
        },
        error => {
          this.toastr.warning("Unable to connect to server");
        }
      );
  }
  /* /Get Pro Vanlynk Services */
  StaffVanlynkServices() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/getActiveLogin/" + this.loginData.loginCode, options)
      .subscribe(
        data => {
          var response = data.json();
          if (response.status == 200) {
            this.numberOfStaff = response.response[0];
            console.log("Number of Staff:" + JSON.stringify(this.numberOfStaff));
            //console.log("ValueEnableVanlynkLicenses:" + JSON.stringify(this.enableVanlynkLicenses.value.noOfLicenses));
            if (this.numberOfStaff.ActiveLogins < this.enableVanlynkLicenses.noOfLicenses) {
              this.router.navigate(['/proStaff/addStaff2'])
            }
            else if (this.numberOfStaff.ActiveLogins = this.enableVanlynkLicenses.noOfLicenses) {
              alert(" Cannot Add Staff, No Free Vanlynk Licenses, Please add Vanlynk licenses from Subscription->Vanlynk licenses")
            }
            else {
              alert(" Cannot Add Staff, No Free Vanlynk Licenses, Please add Vanlynk licenses from Subscription->Vanlynk licenses")
            }
            return false;
          } else {
            this.toastr.warning("No Records Found");
          }
        },
        error => {
          this.toastr.warning("Unable to connect to server");
        }
      );
  }
  headerListstaff() {
    this.router.navigate(["/proStaff/listStaff"]);
  }

  Home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
