  checkAll(check) {
    if (check == true) {
      this.tableAreaExpertiseData.forEach(e => {
        e.checked = true;
      });
    } else {
      this.tableAreaExpertiseData.forEach(e => {
        e.checked = false;
      });
    }
  }

  changeExpertise(data) {
    if (data.checked == true) {
      var tableAreaExpertiseDataChecked = this.tableAreaExpertiseData.filter(
        val => val.checked == true
      );
      this.finalArray = this.selectedData.push(tableAreaExpertiseDataChecked);

      if (tableAreaExpertiseDataChecked.length == 0) {
        this.checkAllStatus = false;
      } else if (tableAreaExpertiseDataChecked.length > 0) {
        this.checkAllStatus = true;
      }
      return false;
    } else if (data.checked == false) {
      var tableAreaExpertiseDataChecked = this.tableAreaExpertiseData.filter(
        val => val.checked == true
      );
      if (tableAreaExpertiseDataChecked.length > 0) {
        this.checkAllStatus = true;
      } else if (tableAreaExpertiseDataChecked.length == 0) {
        this.checkAllStatus = false;
      }
      return false;
    }
  }
