  /*--TASK #553, #554: Set CheckBox Area of Expertise --*/
  setCheckBoxAreaOfExpertise() {

    for (var i = 0; i < this.listEmployeeSkillsData.length; i++) {
      for (var j = 0; j < this.tableAreaExpertiseData.length; j++) {
        if (this.listEmployeeSkillsData[i].categoryCode === this.tableAreaExpertiseData[j].categoryCode && this.listEmployeeSkillsData[i].subcategoryCode === this.tableAreaExpertiseData[j].subcategoryCode) {
          this.selectedData.push(this.tableAreaExpertiseData[j])
          this.tableAreaExpertiseData[j].checked = true;
        }

      }

    }
  }

