  /* -- /Reset Password -- */
  passwordSelection(pass) {

    this.newPassword = pass
    this.passData = false;

    if (this.newPassword == '') {
      this.passReason = true;
    }
    else {

      this.passReason = false
    }
    if (this.newPassword == undefined || this.newPassword == null || this.newPassword.length >= 6 || this.newPassword == '') {
      this.passmaxData = false;
    }
    else {
      this.passmaxData = true;
    }
  }
  verifyPasswordSelction(verifyPassword) {

    this.newverifyPassword = verifyPassword

    if (this.newverifyPassword == '') {
      this.passData = true
      // this.pwMismatch = false;
    }
    else {

      this.passData = false
    }
    if (this.newverifyPassword == undefined || this.newverifyPassword == null || this.newverifyPassword.length >= 6 || this.newverifyPassword == '') {
      this.equicondition = false;
    }
    else {
      this.equicondition = true;
    }
    // if (this.newverifyPassword.length < 6) {
    //   this.enqvicondition = true;
    // }
    if (this.newPassword !== this.newverifyPassword) {

      this.pwMismatch = true;
    }
    if (this.newPassword == this.newverifyPassword) {
      this.pwMismatch = false;
    }
  }

