const MAIL_PATTERN = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
const isEmail: any = email => MAIL_PATTERN.test(email);

const isValidForm = (formData, fields=[]) => {
  return !fields.some(key => {
   const {errors, value } = formData.get(key);
   // if filed is having error or if value empty
   return errors || !value
  })
}
