  validateTime(selectedDate) {
    this.timeList = this.timeList.map(date => {
      if (selectedDate.id !== date.id) return date;
      const toTime = moment(date.toTime, "hh:mm A", true);
      const fromTime = moment(date.fromTime, "hh:mm A", true);
      let isError = false;
      if (moment.isMoment(toTime) && moment.isMoment(fromTime)) {
        isError = fromTime.isSameOrAfter(toTime)
      }
      return { ...date, isError };
    });
  }
