  onEndTime(endTime, selectedDate) {
    var today = this.dateConversion(new Date());
    const startDate = today;
    const startTime = this.addStaffEmployee.value.startTime;
    const endDate = today;
    this.timeList = this.timeList.map(date => {
      if (selectedDate.id !== date.id) return date;
      const startDateTime = moment(startDate + startTime, 'YYYY-MM-DDhh:mm');
      const endDateTime = moment(endDate + endTime, 'YYYY-MM-DDhh:mm');
      let isError = false;
      if (moment.isMoment(startDateTime) && moment.isMoment(endDateTime)) {
        isError = startDateTime.isSameOrAfter(endDateTime)
      }
      return { ...date, isError };
    })
  }
