import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { delay } from 'q';
import { multiplewizard } from '../../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';

import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Global } from '../../../../global/global';
import { CustomValidators } from '../../../../utilities/customValidators';

@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-staff-dashboard',
  templateUrl: './staff-dashboard.component.html',
  styleUrls: ['./staff-dashboard.component.scss']
})
export class StaffDashboardComponent implements OnInit {
  leadsData: any;
  listOfLeadsData: any;
  loginData: any;
  leadsForStaff: any = [];
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.beyond.beyondAdmin();
    this.listOfLeads();
    this.getLeadFromStaff(this.loginData.emailId)
  }

  listOfLeads() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetailsList', options)
      .subscribe(data => {
        this.leadsData = data.json();
        if (this.leadsData.status === 200) {
          this.listOfLeadsData = this.leadsData.response;
          for (var i = 0; i < this.listOfLeadsData.length; i++) {
            if (this.listOfLeadsData[i].emergency == "Yes") {
              console.log("list leads" + this.listOfLeadsData[i].emergency)
              this.listOfLeadsData[i].colorRed = true;
              this.listOfLeadsData[i].color = false;
            } else {
              this.listOfLeadsData[i].colorRed = false;
              this.listOfLeadsData[i].color = true;
            }
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  getLeadFromStaff(emailId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadFromStaff/' + emailId, options)
      .subscribe(data => {
        var leads = data.json();
        if (leads.status === 200) {
          for (var i = 0; i < leads.response.length; i++) {
            for (var j = 0; j < this.leadsData.response.length; j++) {
              if (leads.response[i].leadCode === this.leadsData.response[j].leadId) {
                this.leadsForStaff.push(this.leadsData.response[j])
              }
              if (j == this.leadsData.response.length - 1) {
                this.leadsForStaff.forEach((e) => {
                  if (e.emergency == 'Yes') {
                    e.colorRed = true;
                    e.color = false;
                  } else {
                    e.colorRed = false;
                    e.color = true;
                  }
                })
              }
            }
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }



}
