  // start view staff
  viewStaff() {
    this.spinner.show()
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewEmployee/' + this.viewDataValues.employeeId, options)
      .subscribe(data => {
        var staff = data.json();
        if (staff.status == 200){
          this.spinner.hide()
          this.staffData = staff.response[0];
          this.staffData.pdfName = "";
          this.staffData.managerName = this.staffData.managerName.replace(/\./g, ' ')
          if (this.staffData.documents) {
            this.staffData.pdfName = this.staffData.documents;
            console.log("pdf" + JSON.stringify(this.staffData.pdfName))
            this.staffData.pdfName = this.staffData.documents;
            this.staffData.documents =
              // Global.url +
              // '/'+
              // "/pdf/" +
              this.staffData.documents;
            console.log("pdf documents" + JSON.stringify(this.staffData.documents))
            // this.staffData.documents =Global.url +"/pdf/" + this.staffData.documents;
          }
          if (this.staffData.image == '../../assets/img/no-image.png') {
            this.staffData.image = this.staffData.image
            console.log("IMAGE DATA"+this.staffData.image)
          } else {
            this.staffData.image = Global.url + this.staffData.image
            console.log("IMAGE DATA2222" + this.staffData.image)
          }
          this.viewEmployeeSkills();
          this.viewActivejobs();
          this.viewActivejobscount();
          this.conversionRate();
          console.log("View Staff Data" + JSON.stringify(this.staffData))
          this.dataStaff = true;
        }
        else {
          this.spinner.hide()
          this.dataStaff = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
