  //Starts view Time card settings for Employee
  viewTimeCardSetngEmp() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewTimeCardSettEmp/' + this.viewDataValues.employeeId, options)
      .subscribe(data => {
        var viewTimeCard = data.json();
        this.timeCardData = viewTimeCard.response;
        console.log("timeCardData" + JSON.stringify(this.timeCardData))
        for (var i = 0; i < this.timeCardData.length; i++) {
          //   console.log("in for loop" + this.timeCardData[i].fromTime)
          this.hours = this.timeCardData[i].fromTime.split(":")[0]
          this.minutes = this.timeCardData[i].fromTime.split(":")[1]
          this.outHours = this.timeCardData[i].toTime.split(":")[0]
          this.outMinutes = this.timeCardData[i].toTime.split(":")[1]
          this.fromTimeValue = this.hours + ":" + this.minutes
          this.toTimeValue = this.outHours + ":" + this.outMinutes

          this.timeCardData[i].fromTime = this.fromTimeValue
          this.timeCardData[i].toTime = this.toTimeValue

          //   console.log("timeCardData" + JSON.stringify(this.timeArray))
          //   // this.timeCardData = this.timeArray


          //   if (this.timeCardData[i].onCall == 1 && this.timeCardData[i].dayOff == "") {
          //     console.log("checking oncall")
          //     this.timeCardData[i].checked = true
          //     this.checkbox = true
          //   } else if (this.timeCardData[i].onCall == "" && this.timeCardData[i].dayOff == 1) {
          //     console.log("Unchecking oncall")
          //     this.timeCardData[i].checked = false
          // }
          console.log("timeCardData" + JSON.stringify(this.timeCardData[i]))
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
