  downloadImage(image, key) {
    console.log('ds' + image)
    this.downloadFile(image).subscribe(
      (res) => {
        saveAs(res, key + ".pdf"); //if you want to save it - you need file-saver for this : https://www.npmjs.com/package/file-saver
        var fileURL = URL.createObjectURL(res);
        
        window.open(fileURL);
      }
    )
  }
  downloadFile(url) {
    //this.url = url;
    return this.http.get(Global.url + url, { responseType: ResponseContentType.Blob }).pipe(map(
      (data: any) => {
        return new Blob([data])
      }
    ))
  }

  //DOCUMENT DOWNLOAD PDF
  downloadDocument(path) {
    this.downloadPDF(path)
  }
  //DOCUMENT DOWNLOAD PDF
  //PDF DOWNLOAD SERVICE
  downloadPDF(path) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers, responseType: ResponseContentType.Blob });
    this.http.get(path, options).pipe(map((data: any) => {
      let blob = new Blob([data], {
        type: 'application/pdf'
        // type: 'application/octet-stream' // for excel 
      });
      var link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = new Date().getTime() + '.pdf';
      link.click();
      window.URL.revokeObjectURL(link.href);

    })).subscribe((result: any) => {
    });
  }
