  listOfUploads() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/viewEmpDocuments/" + this.viewDataValues.employeeId, options).subscribe(data => {
      this.uploadData = data.json();
      if (this.uploadData.status == 200) {
        this.listOfUploadsData = this.uploadData.response;
        // console.log("rojaaaa"+JSON.stringify(this.listOfUploadsData))
        this.dataUpload = true;
        this.uploadData.pdfName = "";
        this.viewDoc = Global.url + this.uploadData.documentName
        if (this.uploadData.documentName) {
          this.uploadData.pdfName = this.uploadData.documentName;
          this.writeupData.writeupsDocument =
            Global.url +
            "/pdf/" +
            this.writeupData.writeupsDocument;
        }
      } else {
        this.dataUpload = false;
        console.log("NO Records FOund");
      }
    });
  }

