   /*-- View Active Jobs conversionrate --*/
   conversionRate() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/getemployeeIdActiveJobsConversationRate/" + this.viewDataValues.employeeId, options)
    .subscribe(data => {
      this.activejobscountData = data.json();
      console.log("View active Jobs: " + JSON.stringify(this.activejobscountData));
      if (this.activejobscountData.status == 200) {
        this.conversionrateData = this.activejobscountData.response[0].completedCount;
        console.log("conversionrateData: " + JSON.stringify(this.conversionrateData));
        console.log("activecountJobs: " + JSON.stringify(this.activecountJobs));
        this.rateofConversion = (this.conversionrateData / this.activecountJobs) * 100
        console.log("rateofConversion: " + JSON.stringify(this.rateofConversion));       
      } else {
        
        console.log("NO Records FOund");
      }
    });
  }
