// Search Work Order Starts
searchData() {
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let obj = {
    "employeeId":this.viewDataValues.employeeId,
    "startDate": this.searchdateJobs.value.startDate,
    "endDate": this.searchdateJobs.value.endDate,
 
  }
  console.log(obj)
  this.http.post(Global.url + '/searchDates',obj )
  .subscribe((data: any) => {
    this.staffactiveJobs = data.json();
    console.log("work order" + JSON.stringify(this.staffactiveJobs))
    if (this.staffactiveJobs.status == 200) {
      this.activeJobs = this.staffactiveJobs.response;
      // alert(JSON.stringify(this.activeJobs))
      console.log("searchactivejobsData values" + JSON.stringify(this.activeJobs))
      this.showEmployeeData = true
    } else { 
      this.activeJobs = []     
     this.showEmployeeData = false;
      console.log('The else')
    }
  })
}
