import { Http } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

import { ImageUpload } from "src/utilities/imageupload";
import { Global } from "../../../../../../global/global";
import { Beyond } from "../../../../../../utilities/beyond";

@Component({
  providers: [Beyond, ImageUpload],
  selector: 'app-upload-document',
  templateUrl: './upload-document.component.html',
  styleUrls: ['./upload-document.component.scss']
})
export class UploadDocumentComponent implements OnInit {
  addUpload: FormGroup;
  percentage: any;
  imagePath: string;
  submitted: boolean = false;
  progressShow: boolean = false;
  imagePreview: string = "assets/img/no-image.png";
  randamvalue: any;
  file: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public toastr: ToastrService,
    public activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private http: Http
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    const loginData = JSON.parse(localStorage.getItem("login"));
    this.addUpload = this.formBuilder.group({
      description: ["", Validators.required],
      documentName: [""],
      employeeId: [""]
    });
  }

  saveDocument() {
    this.router.navigate(['/proStaff/viewStaff2'])
  }

  cancelDocument() {
    this.router.navigate(['/proStaff/viewStaff2'])
  }
  //Select Image - Starts Here
  select(event) {
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      this.file = file;
      this.showImagePreview(file);
    }
  }

  /*To show image preview*/
  showImagePreview(file) {
    const picReader = new FileReader();
    picReader.onload = (e: any) => {
      this.imagePreview = e.target.result;
    };
    picReader.readAsDataURL(file);
  }
  //HANDLEIMAGE(FILE) - ENDS HERE

  adduploadData() {
    const viewDataValues = JSON.parse(localStorage.getItem("viewData"))
    const formData = new FormData();
    formData.append("description", this.addUpload.value.description);
    formData.append("file", this.file);
    formData.append("employeeId", viewDataValues.employeeId);

    this.http.post(Global.url + '/addUpload', formData).subscribe(
      data => {
        const response = data.json();
        if (response.status === 200) {
          this.toastr.success("Document Added Successfully");
          this.router.navigate(["/proStaff/viewStaff2"]);
        } else {
          this.toastr.warning("INCORRECT DETAILS !");
        }
      },
      error => {
        this.toastr.warning("INCORRECT DETAILS !");
      }
    );
  }
  onSubmit() {
    this.submitted = true;
    if (this.addUpload.value.description == "") {
      // this.toastr.warning("Please Enter Writeup comment.");
      return;
    } else {
      this.adduploadData()
    }
  }
}
